<template>
  <div class="wizard">
    <ul class="wizard__steps">
      <li class="wizard__step"
        :class="{'active': currentStep >= index}"
        :style="{ width: 100/steps.length + '%' }"
        v-for="(step, index) of steps" :key="index + '-' + getRandom()">
        <span class="wizard__step__line"></span>
        <span class="wizard__step__label">{{step.label}}</span>
        <span class="wizard__step__indicator"></span>
      </li>
    </ul>
    <span
      class="wizard__arrow"
      :style="{ left: arrowPosition }">
    </span>
    <div class="wizard__body">
      <div class="wizard__body__step">
        <slot :name="currentSlot"></slot>
      </div>
      <div class="wizard__body__actions clearfix">
        <a
          v-if="backEnabled"
          class="wizard__back pull-left"
          @click="goBack()">
          <i class="vgw-icon vgw-prev"></i>
          <span>{{previousStepLabel}}</span>
        </a>
        <a
          v-if="currentStep == 0 && wizType=='team'" class="wizard__next pull-right"
          @click="goNextTeamName()">
          <span>{{nextStepLabel}}</span>
          <i class="vgw-icon vgw-next"></i>
          <!-- <img src="../images/next.png" alt="next icon"> -->
        </a>
        <a
          v-if="currentStep == 0 && wizType=='player'" class="wizard__next pull-right"
          @click="goNextPlayersOne()">
          <span>{{nextStepLabel}}</span>
          <i class="vgw-icon vgw-next"></i>
        </a>
        <a
          v-if="currentStep == 1 && wizType=='team'" class="wizard__next pull-right"
          @click="goNextPlayers()">
          <span>{{nextStepLabel}}</span>
          <i class="vgw-icon vgw-next"></i>
        </a>
        <!--
        <a
          v-if="currentStep == 1 && wizType=='player'" class="wizard__next pull-right"
          @click="goNextPlayersTwo()">
          <span>{{nextStepLabel}}</span>
          <i class="vgw-icon vgw-next"></i>
        </a>-->
        <!--
        <a
          v-if="currentStep == 2 && wizType=='team'" class="wizard__next pull-right"
          @click="goNext()">
          <span>{{nextStepLabel}}</span>
          <i class="vgw-icon vgw-next"></i>
          <img src="../images/next.png" alt="next icon">
        </a>-->

        <a
          v-if="currentStep == steps.length - 1" class="wizard__next pull-right final-step" @click="goNext()">
          {{finalStepLabel}}
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {

  name: 'tkd-wizard',

  props: {
    steps: {},
    previousStepLabel: {default: 'Back'},
    nextStepLabel: {default: 'Next'},
    finalStepLabel: {default: 'Save'},
    onNext: {},
    onBack: {},
    submitTeam: {},
    submitPlayer: {}
  },

  data () {
    return {
      currentStep: 0,
      teamName: '',
      wizType: 'team'
    };
  },
  computed: {
    arrowPosition() {
      var stepSize = 100/this.steps.length;
      var currentStepStart = stepSize * this.currentStep;
      var currentStepMiddle = currentStepStart + (stepSize/2);
      if(this.steps.length == 1)
        return 'calc('+currentStepMiddle+'%)'
      else
        return 'calc('+currentStepMiddle+'% - 14px)'
    },
    currentSlot() {
      return this.steps[this.currentStep].slot;
    },
    backEnabled() {
      return this.currentStep != 0;
    }
  },
  mounted() {
    //do something after mounting vue instance
    if(this.steps.length == 2){
      this.wizType = 'player'
    }
    else{
      this.wizType = 'team'
    }
  },
  methods: {
    goNext(skipFunction) {
      console.log('NextClicked');
      if(this.steps.length == 2){
        this.submitPlayer();
      }
      else{
        var id_str = jQuery('.teamIdEntry').val();
        var tok_str = jQuery('.teamTokenEntry').val();
        if(id_str===''){
          alert('Please enter a unique team ID');
        }
        else {
          var self = this;
          firebase.database().ref('TeamSportfolios').child(id_str).once('value', function(snapshot){
            if(snapshot.val()){
              alert('That ID already exists! Please choose another one');
            }
            else {
              if (tok_str==='') {
                alert('Please enter a team token');
              }
              else {
                alert('Good Choice! That ID\'s unique');
                self.submitTeam();
              }
            }
          })
        }
      }
    },
    getRandom(){
      return Math.random().toString(36).substring(2,7);
    },
    goNextPlayersOne (skipFunction){
      //add code to validate players info here...
      this.currentStep++;
      return true;
    },
    goNextPlayersTwo (skipFunction){
      //add code to validate players info here...
      this.currentStep++;
      return true;
    },
    goNextTeamName (skipFunction) {
        var tn_str = jQuery('.teamNameEntry').val();
        if(tn_str===''){
          alert('Please Enter a Team Name');
          return false;
        }
        else {
          this.teamName = tn_str;
          this.currentStep++;
          return true;
        }

    },
    goNextPlayers (skipFunction) {
      if (this.currentStep < this.steps.length-1) {
        this.currentStep++;
        this.$emit('SetDefaultPid')
      }
    },
    goBack (skipFunction) {
      if (!skipFunction && typeof this.onBack == 'function'){
        if(!this.onBack(this.currentStep)) {
          //returned false. don't do anything
          return;
        }
      }
      if (this.currentStep > 0) {
        this.currentStep--;
      }
    },
  }
};
</script>

<style lang="css" scoped>
/* Utilities
*******************************/

.pull-left{
  float:  left !important;
}

.pull-right{
  float:  right !important;
}

.clearfix::after {
  display: block;
  content: "";
  clear: both;
}

/* Header Steps
*******************************/
.wizard {
  position: relative;
  width:  100%;
  color: rgb(252,102,0);
}

.wizard__steps{
  list-style-type:  none;
  text-align: justify;
  -ms-text-justify: distribute-all-lines;
  text-justify: distribute-all-lines;
  padding:  0;
  height:  70px;
  position:  relative;
}

.stretch {
  width: 100%;
  display: inline-block;
  font-size: 0;
  line-height: 0
}

.wizard__step{
  height: 70px;
  vertical-align: bottom;
  display: inline-block;
  text-align: center;
  position:  relative;
}

.wizard__step:not(:first-child) .wizard__step__line{
  position: absolute;
  width:  100%;
  left:  -50%;
  bottom:  12px;
  height:  3px;
  background-color: white;
}

.wizard__step__indicator{
  box-sizing: content-box;
  display:  block;
  width:  16px;
  height:  16px;
  background-color: rgb(224,0,16);
  border-radius: 50%;
  border: 3px solid #fff;
  position:  absolute;
  left:  50%;
  margin-left:  -10px;
  bottom:  2px;
  z-index: 1;
}

.wizard__step.active .wizard__step__indicator{
  background-color: rgb(242,209,24);
}

.wizard__step.active:not(:first-child) .wizard__step__line{
  background-color: rgb(242,209,24); /* green */
}

.wizard__step__label{
  color: rgb(242,209,24);
  font-weight: bold;
}

/* Wizard body
*******************************/
.wizard__body{
  margin-top:  30px;
  min-height:  400px;
  margin-left:  50px;
  margin-right:  50px;
  border:  3px solid white;
  background-color: white;
  position: relative;
  border-radius: 5px;
  padding-bottom: 50px;
}

.wizard__body__step{
  padding:  16px;
}

.wizard__arrow{
  position:  absolute;
  display: block;
  width:  30px;
  height:  30px;
  border:  1px solid white;
  top:  85px; /* height of step + body margin -15 */
  border-top-right-radius: 5px;
  background-color: #fff;
  border-left: none;
  border-bottom:  none;
  transform: rotate(-45deg);
  z-index: 2;
  -webkit-transition: left 0.3s;
  -o-transition: left 0.3s;
  transition: left 0.3s;
}

/* Wizard body
*******************************/
.wizard__body__actions{
  position:  absolute;
  bottom:  0px;
  height:  50px;
  width:  100%;
  border-top:  1px solid rgb(242,209,24);
  background-color: rgba(252,102,0, 0.7);
}

.wizard__body__actions a{
  width:  120px;
  height:  100%;
  display: block;
  background-color: rgb(235,95,17);
  color: white;
  font-weight: bold;
  text-align: center;
  line-height: 50px;
  cursor: pointer;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
}

.wizard__body__actions a>.vgw-icon, .wizard__body__actions a>span{
  display: inline-block;
  vertical-align: middle;
  position: relative;
}

.wizard__body__actions .vgw-icon::after{
  position: absolute;
  content: "";
  transform: rotate(-45deg);
  width: 8px;
  height: 8px;
  top: 50%;
  margin-top: -5px;
}

.vgw-icon.vgw-next::after{
  border-bottom: 2px solid rgb(242,209,24);
  border-right: 2px solid rgb(242,209,24);
}

.vgw-icon.vgw-prev::after{
  border-top: 2px solid white;
  border-left: 2px solid white;
  left: -10px;
}

.wizard__body__actions a:hover{
  background-color: rgb(224,0,16);
  color: white;
}

.wizard__body__actions a.final-step{
  background-color: rgb(252,102,0);
}

.wizard__body__actions a.final-step:hover{
  background-color: rgb(224,0,16);
}

</style>



